:root {
  --btn-bg-color: violet;
  --bd-bg-color: #1e1d1d;
  --text-color-white: white;
  --text-color-black: black;
  --border-color: #ebebeb;
  --box-shadow-color: #c8c3c3;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  box-sizing: border-box;
  background-color: var(--bd-bg-color);
  color: var(--text-color-white);
  justify-content: space-between;
  display: flex;
}

header div {
  margin: .5rem 0 0 1.5rem;
  padding: .5rem;
  font-size: 1.5rem;
}

header form {
  margin: auto;
}

header input {
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  width: 65%;
  padding: 5px;
}

header button {
  background-color: var(--btn-bg-color);
  color: var(--text-color-black);
  cursor: pointer;
  border: none;
  border-radius: 0 5px 5px 0;
  margin-top: .5rem;
  margin-left: -4px;
  padding: 5px;
}

#productHeader {
  margin-left: 2rem;
  font-size: 2rem;
}

.productCardStyle {
  border: 1px solid var(--border-color);
  box-shadow: 5px 5px 10px 2px var(--box-shadow-color);
  border-radius: 8px;
  margin: 1.5rem;
  padding: .5rem;
}

img {
  width: 300px;
}

.addBtnStyle {
  background-color: var(--btn-bg-color);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: large;
}

dialog {
  border: none;
  border-radius: 10px;
}

dialog button {
  cursor: pointer;
  background: none;
  border: none;
  position: relative;
  right: -280px;
}

::backdrop {
  background-color: var(--bd-bg-color);
  opacity: .9;
}

@media (width >= 450px) {
  header {
    height: 90px;
  }

  header div {
    margin: auto;
    padding: .5rem;
    font-size: 2rem;
  }

  header form {
    margin: auto;
  }
}

@media (width >= 750px) {
  img {
    width: 400px;
  }

  .productCardStyle {
    border: 1px solid var(--border-color);
    box-shadow: 5px 5px 10px 2px var(--box-shadow-color);
    border-radius: 8px;
    gap: 10rem;
    margin: 1.5rem;
    padding: 1rem;
    display: flex;
  }

  .addBtnStyle {
    background-color: var(--btn-bg-color);
    cursor: pointer;
    border: none;
    border-radius: 5px;
    width: 200px;
    padding: 10px;
    font-size: large;
  }
}

